import { SideNav, SideNavProps } from '@carbon/react';
import { ReactNode, forwardRef } from 'react';
import omit from 'lodash.omit';

export interface WagLeftNavRailProps extends SideNavProps {
  children?: ReactNode;
  className?: string;
}

export const WagLeftNavRail = forwardRef<HTMLElement, WagLeftNavRailProps>(
  (
    props: WagLeftNavRailProps,
    ref 
  ) => {
    const filteredProps = omit(props, [
      'isFixedNav',
      'expanded',
      'isChildOfHeader',
    ]);
  
    return (
      <SideNav
      isFixedNav
      expanded={true}
      isChildOfHeader={false}
      {...filteredProps}
      ref={ref}
    >
      {props.children}
    </SideNav>
    );
  });