import { ReactNode, useEffect, useState, useId, useRef } from 'react';
import {
  SideNavItems,
  SideNavMenu,
  SideNavMenuItem,
} from '@carbon/react';
import omit from 'lodash.omit';
import { ArrowDownIcon } from '@wag/icons-react';
import PropTypes from 'prop-types';



export interface WagSideNavItemsProps extends React.AllHTMLAttributes<HTMLBaseElement>  {
  children?: ReactNode;
  className?: string;
}

export interface WagSideNavMenuProps extends React.AllHTMLAttributes<HTMLBaseElement> {
  children?: ReactNode;
  className?: string;
  title: string;
}
export interface WagSideNavMenuItemProps {
  children?: ReactNode;
  className?: string;
  href?: string;
  as?: string | undefined;
}

SideNavMenu.propTypes = {
  ...SideNavMenu.propTypes,
  title: PropTypes.any
}
// Wrapper component for Carbon Design System Accordion

export const WagSideNavItems = (props: WagSideNavItemsProps) => {
  // Extracting additional properties from props

  const filteredProps = omit(props, ['isSideNavExpanded']);

  return <SideNavItems {...filteredProps}>{props.children}</SideNavItems>;
};

export const WagSideNavMenu = (props: WagSideNavMenuProps) => {
  // Extracting additional properties from props
  const uniqueId = useId();
  const ref = useRef(null);
  const [labelledBy, setLabelledBy] = useState(false);
  const filteredProps = omit(props, [
    'defaultExpanded',
    'isActive',
    'large',
    'isSideNavExpanded',
  ]);
  const [viewportSize, setViewportSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });
  const id = filteredProps.id || uniqueId;
  const handleResize = () => {
    console.log('window.innerWidth', window.innerWidth);
    setViewportSize({ width: window.innerWidth, height: window.innerHeight });
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);

    // Cleanup the event listener on component unmount
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    if(!labelledBy && ref.current){
      (ref.current as HTMLElement)?.parentNode?.querySelector('ul')?.setAttribute('aria-labelledby',id);
      setLabelledBy(true)
    }
  },[labelledBy, id]);
  
  return (

    <SideNavMenu 
      defaultExpanded={viewportSize.width > 767}
      renderIcon={() => <AccordionIcon />}
      ref={ref}
      {...filteredProps}
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-expect-error SideNavMenu prop only accepts string but it can also be react component.
      title={<span id={id}> {filteredProps.title} </span>}
    >
      {props.children}
    </SideNavMenu>
  );
};

export const WagSideNavMenuItem = (props: WagSideNavMenuItemProps) => {
  // Extracting additional properties from props

  const filteredProps = omit(props, ['isActive']);
  return (
    <SideNavMenuItem href="https://www.walgreens.com/" {...filteredProps}>
      {props.children}
    </SideNavMenuItem>
  );
};

const AccordionIcon = () => {
  return (
    <ArrowDownIcon size='md'  />
  );
};
